import { useEffect, useState } from "react";

const FetchLocations = () => {
  const [elements, setElements] = useState(<ul>piip</ul>);
  useEffect(() => {
    const fetchData = async () => {
      let result = await fetch(
        "https://locations-api-harjoite.herokuapp.com/api/locations"
      );
      let data = await result.json();
      console.log(data);
      /* modifyUI(data); */
      const newElements = data.map((object, index) => {
        return <li key={index}>{Object.values(object).join(" - ")}</li>;
      });
      console.log("ele", newElements);
      setElements(newElements);
    };
    return fetchData();
  }, []);

  console.log(elements);
  return elements;
};

export default FetchLocations;
